import React from 'react';
import styled, { keyframes } from 'styled-components';
import { themeGet } from 'styled-system';

import getSliderFloor from '../../getSliderFloor';
import floor from './floor-3.svg';
import bear from './bear.svg';
import SVG from '../../FloorSVG';

import projects from '../../projects/3';
import floors from '../../../Elevator/floors.json';

import Box from '../../../../components/Box';
import Container from '../../../../components/Container';
import BackgroundImage from '../../../../components/BackgroundImage';

const play = keyframes`
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 100% 0;
  }
  100% {
    background-position: 0 0;
  }
`;

const Animatable = styled(({ animate, ...props }) => <BackgroundImage {...props} />)`
  background-position: 0 0;
  ${({ animate }) => animate && `
    animation: ${play} 1s steps(4) infinite;
  `}
`;

const StyledSvg = styled(SVG)`
  .floor-btn {
    cursor: pointer;
    &:hover {
      rect:nth-of-type(2) {
        fill: ${themeGet('colors.yellow')};
      }
      polygon {
        fill: white;
        stroke: white;
      }
    }
  }
  image {
    cursor: pointer;
  }
  .floor-morebtn {
    cursor: pointer;
    &:hover {
      rect:nth-of-type(2) {
        fill: ${themeGet('colors.yellow')};
      }
      text {
        fill: black;
      }
    }
  }
`;

const SliderFloor = getSliderFloor(floors[1].key, projects, 588);

class Floor3 extends SliderFloor {
  render() {
    return (
      <Container innerRef={this.handleContainerRef} position="relative">
        <StyledSvg
          src={floor}
          ratio={665.52 / 1192.5}
          onLoad={this.handleSvgLoad}
        />
        {/* <Box position="absolute" top="0" right="9.5%" left="9.5%">
          <Animatable animate src={bear} ratio={555.23 / (4245.12 / 5)} />
        </Box> */}
      </Container>
    );
  }
}

export default Floor3;



